/* Extra small devices (phones, 600px and down) */
@media only screen and (min-width: 320px) and (max-width: 600px){
   
   
   .bg-main {

      video {
         min-width: auto;
         min-height: 100%;;
      }
   }
}


@media (max-width: 700px){
   .about {
      .about-items {
         width: 100%;

         img {
            padding: 20px;
         }
         h5 {
            font-size: 1em;
         }
      }
   }

 
   .grid {
      display: flex;
      flex-direction: column;
   }

   .projects {

      .container-big {
         width: 95%;
      }
   }

   footer {
      padding: 20px 0 20 0!important;
   }
}


@media (max-width: 700px){
   .nosotros-header,.servicios-header, .proyectos-header, .contacto-header {
      height: 300px!important;
   }

   .about {
      .about-items {
         li:nth-child(1){

            &:after {
               right: -30%;
            }
         }
         li:nth-child(2){
            &:after {
               right: -10%;
            }
         }
      }
   }
}
